<script>

import { real } from "./js/jquery.min.js"
import { jquery } from "./js/jquery.orgchart.js"

import axios from 'axios';
import { VoBasic } from 'vue-orgchart'

export default {
    name: 'zgjx',
    inject:["reload"],
    data(){
        return{
            input: '',
            currentPage1: 1,
            currentPage2: 1,
            currentPage3: 1,
            currentPage4: 1,
            currentPage5: 1,
            total:100,
            total1:100,
            total2:100,
            total3:100,
            total4:100,
            pagesize:10,
            currpage:1,
            tableData: [],
            tableData1: [],
            tableData2: [],
            tableData3: [],
            tableData4: [],
            options: [],
            value: '',
          //  options2: [],
          //  value2: [],
            options3: [],
            value3: '',
            datasource : {}
        }
    },
    methods: {
      deleteRow(index,tableData4) {
          console.log(index,tableData4)
          localStorage.setItem("ycid",tableData4[index].user_id)
      },
      open2() {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
             var that = this;
        let id = localStorage.getItem("userId");
        let ycid = localStorage.getItem("ycid")
        console.log(id)
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/getscjk.action",
            data:{
                user_id:ycid,
                mon_id:id
            },
            dataType: "json",
            success: function (res) {
                console.log(res)
                if(res.mess == true){
                    that.$message({
                        type: 'success',
                        message: '!'
                    });
                }else{
                   that.$message({
                        type: 'error',
                        message: '!'
                    }); 
                }
            that.reload();
            }
        })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      },
      quxiao(e){
          this.$refs.mask.style.display = "none";
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
        this.pagesize = val
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.currpage = val
      },
    //   selectTrigger2(e){
    //     console.log(e)
    //     localStorage.setItem("id",e);
    //   },
      selectTrigger3(e){
        console.log(e)
        localStorage.setItem("tid",e);
      },
      get:function(){
          var that = this;
        let id = localStorage.getItem("user_id");
        let tid = localStorage.getItem("tid")
        console.log(id)
        let formData = new FormData();
        formData.append('user_id',tid);
        formData.append('mon_id',id);
        axios.post(this.baseUrl+'/ydjxzg/mb/gettjjk.action',formData)
            .then(function (res) {
                console.log(res);
                that.reload();
            })
        // $.ajax({
        //     type: "post",
        //     url: this.baseUrl+"/ydjxzg/mb/gettjjk.action",
        //     data:{
        //         user_id:tid,
        //         mon_id:id
        //     },
        //     dataType: "json",
        //     success: function (res) {
        //         console.log(res)
        //         // that.tableData4 = res.jkryxx;
        //         // that.total4 = res.jkryxx.length
        //        // location.reload();
        //     }
        // }); 
      },
    //   sousuo:function(){
    //     var that = this;
    //     let id = localStorage.getItem("id");
    //     console.log(id)
    //     $.ajax({
    //         type: "post",
    //         url: this.baseUrl+"/ydjxzg/mb/getjkryxx.action",
    //         data:{
    //             user_id:id
    //         },
    //         dataType: "json",
    //         success: function (res) {
    //             console.log(res)
    //             that.tableData4 = res.jkryxx;
    //             that.total4 = res.jkryxx.length
    //         }
    //     }); 
    //   }
    },
    mounted(){
        var that = this; 
        var dj = localStorage.getItem("userdj")
        let usedaid = localStorage.getItem("user_id");
        var query = this.$route.params.core
        console.log(query)
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/jxscore.action",
            data:{
                manager_id:usedaid,
                dj: dj
            },
            dataType: "json",
            success: function (res) {
                console.log(res)
                that.options = res.target
                that.tableData = res.xsjxf;
                that.total = res.xsjxf.length;
                that.tableData1 = res.bmjxf;
                that.total1 = res.bmjxf.length;
                that.tableData2 = res.bmbyts;
                that.total2 = res.bmbyts.length;
                that.tableData3 = res.qxjxf;
                that.total3 = res.qxjxf.length;
                that.tableData4 = res.jkryxx;
                that.total4 = res.jkryxx.length;
                if(res.myscore){
                    res.myscore.target_name = res.myscore.manager_name
                    if(res.myscore.weight ==undefined ){
                        res.myscore.weight ='1'
                    }
                }
               // that.options2 = res.xsjxf;
                that.options3 = res.jkmd;
                that.datasource = res.myscore
                var nodeTemplate = function(data) {
        return `
            <div class="title">${data.target_name}</div>
            <div class="content">${data.progress} (${data.weight})</div>
        `;
        };
        var oc = $('#chart-container').orgchart({
            'data' : res.myscore,
            'nodeTemplate': nodeTemplate,
            'nodeTitle': 'title',
            'createNode': function($node,data){
                // console.log($node,data)
                // document.oncontextmenu = function(e){
                //     return false;   //
                // };
                $node.mousedown(function(e) {
                    if (3 == e.which) {   //3
                        console.log(1232)
                    }
                })
            }
        });
            }
        }); 
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/getjtjx.action",
            data:{
                manager_id:1
            },
            dataType: "json",
            success: function (res) {
                console.log(res)
            }
        }); 
        // var datasource = this.datasource;
    }
}
